	module.exports = {"data":{"site":{"siteMetadata":{"title":"Urban Faubion / Design Technologist"}},"markdownRemark":{"id":"/Users/urban/Documents/Projects/urban.github.io/data/work/multi-media-boardroom/index.md absPath of file >>> MarkdownRemark","html":"<p><a href=\"https://www.daimler.com/en/\">Daimler</a> was in the process of building out several new, state of the art conference room system for their executive board. They wanted to combine the best in breed hardware and software in a seamless user experience.</p>\n<p><a href=\"https://www.frogdesign.com/\">frog</a> was hired to help Daimler evaluate and pick a software partner to power a multi-screen, multi-purpose conference rooms that include remote participation.</p>\n<p>In partnership with <a href=\"https://www.macom.de/en/\">macom</a>, I developed a 60 page “Vendor Briefing” document to be used for evaluating software vendor solutions. Using the Vendor Briefing document and a mock conference room with the required hardware, each vendor would demonstrate the strengths and weaknesses of their software according to several user journey scenarios in a full day presentation. Following the vendor presentations, I lead the evaluation workshop to discuss the different software solutions and provided my recommendation.</p>\n<h3>Activities &#x26; Deliverables</h3>\n<ul>\n<li>facilitated a project kick-off and several client and stakeholder workshops</li>\n<li>wrote and provided creative direction for a 60 page Vendor Briefing document</li>\n<li>conducted 4 on-site software vendor evaluation at Daimler’s AG Headquarters</li>\n<li>created a vendor evaluation criteria document</li>\n<li>organized and lead the vendor evaluation workshop</li>\n</ul>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/Daimler-Page-01-c233d0b53c89f1c514c23d8719b59a5b-d772a.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 888px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 75%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Daimler Vendor Briefing\"\n        title=\"\"\n        src=\"/static/Daimler-Page-01-c233d0b53c89f1c514c23d8719b59a5b-98b5c.png\"\n        srcset=\"/static/Daimler-Page-01-c233d0b53c89f1c514c23d8719b59a5b-6bb4a.png 222w,\n/static/Daimler-Page-01-c233d0b53c89f1c514c23d8719b59a5b-366c9.png 444w,\n/static/Daimler-Page-01-c233d0b53c89f1c514c23d8719b59a5b-98b5c.png 888w,\n/static/Daimler-Page-01-c233d0b53c89f1c514c23d8719b59a5b-d772a.png 1024w\"\n        sizes=\"(max-width: 888px) 100vw, 888px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>","frontmatter":{"title":"Multi Media Boardroom","client":"Daimler"}}},"pathContext":{"slug":"/work/multi-media-boardroom/","previous":{"fields":{"slug":"/work/search-2020/"},"frontmatter":{"title":"Search 20/20","draft":null}},"next":{"fields":{"slug":"/work/matrix/"},"frontmatter":{"title":"Matrix","draft":null}}}}

